import React from "react";
import "../Styles/Contacto.css";

const Contacto = () => {
  return (
    <div className={` contactoContainer`}>
      <div className="content ">
        <h1 className={`text-center titulo`}><span className="text-danger">-</span>Contacto<span className="text-danger">-</span></h1>
        <div className="contact-wrapper animated bounceInUp mt-5 w-75 m-auto">
          <div className="contact-form">
            <form action="">
              <p>
                <label>FullName</label>
                <input type="text" name="fullname" />
              </p>
              <p>
                <label>Email Adress</label>
                <input type="email" name="email" />
              </p>
              <p>
                <label>Phone Number</label>
                <input type="tel" name="phone" />
              </p>
              <p>
                <label>Affair</label>
                <input type="text" name="affair" />
              </p>
              <p className="block">
                <label>Message</label>
                <textarea name="message" rows="3"></textarea>
              </p>
              <p className="block">
                <button>Enviar</button>
              </p>
            </form>
          </div>
          <div className="contact-info m-auto">
            <img
              src="https://res.cloudinary.com/duddljshu/image/upload/v1658471255/WhatsApp_Image_2022-07-22_at_1.27.03_AM_qn41tf.jpg"
              alt=""
              style={{ width: "100%"}}
              className="ri-logo-circle-line    img-logo"
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contacto;
